import { defineStore } from 'pinia'
import { User } from '@/types/Type'

export const useUserDetailsStore = defineStore('userDetails', {
  state: () => ({ 
    searchedKeywords: [] as string[],
    userData: [] as User[],
    individualUserData: {} as User,
    accessToIndividualUserData: false as boolean,
  }),
  getters: {
    getKeywords: (state) => {
      return state.searchedKeywords.slice(-4)
    },
    getLastKeyword: (state) => {
      return state.searchedKeywords[state.searchedKeywords.length - 1] || ''
    },
    getUserData: (state) => {
      return state.userData
    },
    getIndividualUserData: (state) => {
      return state.individualUserData
    },
    getAccessToIndividualUserData: (state) => {
      return state.accessToIndividualUserData
    }
  },
  actions: {
    setUserData(recievedUserData:User[]) {
      this.userData = recievedUserData
    },
    addSearchedKeyword(keyword:string) {
      this.searchedKeywords.push(keyword)
    },
    removeSearchedKeyword(toRemoveKeyword:string) {
      this.searchedKeywords = this.searchedKeywords.filter((searchKeyword:string) => {
        return toRemoveKeyword !== searchKeyword
      })
    },
    setIndividualUserData(userData:User) {
      this.individualUserData = userData
    },
    setAccessToIndividualUserData(shouldAllow:boolean) {
      this.accessToIndividualUserData = shouldAllow
    },
  },
})
